import { useStore } from "../hooks/useStore.js";
import { Cube } from "./Cube.js";

export const Cubes = () => {
  const [cubes] = useStore((state) => [state.cubes]);
  // console.log(cubes);
  return cubes.map(({ key, pos, texture }) => {
    return <Cube key={key} position={pos} texture={texture} />;
  });
};
